import React from "react";
import s from "./ProfileInfo.module.css";

type ProfileInfoProps = {
    id: string,
    name: string,
    dateOfBirth: string,
    city: string,
    education: string,
    site?: string,
    avatar: string
}
export const ProfileInfo = (props: ProfileInfoProps) => {
    return (
        <div className={s.mainContainer}>
            <div className={s.avatarContainer}>
                <img src={props.avatar} className={s.avatar}/>
            </div>
            <div className={s.infoContainer}>
                <div className={s.Name}>{props.name}</div>
                <div className={s.data}>Date of birth: {props.dateOfBirth}</div>
                <div className={s.data}>City: {props.city}</div>
                <div className={s.data}>Education: {props.education}</div>
                <div className={s.data}>Web site: {props.site}</div>
            </div>
        </div>
    )
}